.about {
  color: $color-blue-gray;

  background: $color-blue;

  img {
    display: block;
  }

  p {
    margin: 0;
  }
}

.about__wrapper {
  margin: 0;
}

.about__text {
  margin: 0;
}

.about__title {
  margin: 0;

  font-family: "Euclid Flex", "Arial", sans-serif;
  font-size: $largFontSize;
  line-height: $largLineHeght;
  color: $white;
}
.about__info {
  margin: 0;
  padding: 0;

  line-height: 20px;

  li {
    list-style: none;
  }

  b {
    font-size: $largFontSize + 12;
    color: $white;
  }
}

@media (min-width: $media-breakpoint-sm) {
  margin: 0;
}
