body {
  padding: 0;
  min-width: 320px;

  font-family: "GillSans", "Arial", sans-serif;
  font-size: $baseFontSize;
  line-height: 22px;
  color: $color-base;
}

a {
  color: $color-base;
  text-decoration: none;

  &:hover {
    color: $hover-color;
  }
}

img {
  max-width: 100%;
  height: auto;
}
