.news {
  margin: 0;
}

.events__wrapper {
  margin: 0;
}

.news__title {
  margin: 0;

  font-family: "Euclid Flex", "Arial", sans-serif;
  font-size: $largFontSize;
  line-height: $largLineHeght;
  color: $color-darker-gray;
}

.news__head-link {
  font-size: $baseFontSize - 2;
  line-height: $baseLineHeght + 1;
  letter-spacing: 0.05em;
  color: $color-darker-gray;
}

.news__date {
  font-size: $baseFontSize - 6;
  line-height: $baseLineHeght - 7;
  color: $color-gray;
}

.news__text {
  color: $color-gray;
}

.news__link {
  color: $color-gray;
}

.news__list {
  margin: 0;
  padding: 0;

  img {
    display: block;
  }

  p {
    margin: 0;
  }
}

@media (min-width: $media-breakpoint-sm) {
  margin: 0;
}
