.page-header {
  padding: 0;
}

.page-header__logo {
  margin-right: 20px;
}

.page-header__slogan {
  font-family: "Euclid Flex", "Arial", sans-serif;
  font-size: $baseFontSize - 2;
  line-height: 23px;
}

.page-header__contacts {
  font-size: $baseFontSize - 4;
  line-height: 17px;
  text-decoration: none;
  color: $color-blue;
}

.page-header__navbar-toggler {
  margin: 0;
}

@media (min-width: $media-breakpoint-md) {
  margin: 0;
}
