.page-footer {
  margin: 0;
  padding: 0;

  font-size: $baseFontSize - 6;
  line-height: $baseLineHeght - 7;
  color: $white;

  background-color: $color-blue;

  a {
    color: $white;
  }
}

.page-footer__phone {
  font-size: $baseFontSize - 4;
  line-height: $baseLineHeght - 5;
}

.page-footer__nav {
  a {
    font-size: inherit;
    line-height: inherit;
  }
}

@media (min-width: $media-breakpoint-lg) {
  .page-footer {
    margin: 129px 0 0;
  }
}

@media (min-width: $media-breakpoint-xl) {
  .page-footer {
    margin: 173px 0 0;
  }
}
