.events {
  margin: 0;

  background-color: $color-blue;
}

.events__wrapper {
  margin: 0;
}

.events__title {
  margin: 0;

  font-family: "Euclid Flex", "Arial", sans-serif;
  font-size: $largFontSize;
  line-height: $largLineHeght;
  color: $white;
}
.events__list {
  margin: 0;

  font-size: $baseFontSize;
  line-height: $baseLineHeght + 3;
  color: $color-darker-gray;

  img {
    display: block;
  }

  p {
    margin: 0;
  }
}

@media (min-width: $media-breakpoint-sm) {
  margin: 0;
}
