//Шрифты
@font-face {
  font-family: "GillSans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/Gill-Sans.ttf") format("truetype"),
  url("../fonts/GillSansRegular.woff") format("woff");
}

@font-face {
  font-family: "Euclid Flex";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/EuclidFlex-Regular.woff2") format("woff2"),
  url("../fonts/EuclidFlex-Regular.woff") format("woff");
}
