.nav__list {
  margin: 0;
  padding: 0;

  a {
    font-size: $baseFontSize;
    line-height: 22px;
    text-decoration: none;
    color: $color-blue-gray;

    &:hover {
      text-decoration: underline;
      color: $hover-color;
    }
  }
}

.nav__item {
  list-style: none;
}

@media (min-width: $media-breakpoint-md) {
  .nav-item {
    position: relative;

    &.active,
    &:hover {
      .nav-link {
        &::before {
          content: "";

          position: absolute;
          top: -25px;
          left: 0;

          width: 100%;
          height: 10px;

          background: radial-gradient(50% 50% at 50% 50%, #0092f2 27.6%, #006be9 100%);
        }
      }
    }
  }
}
